import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import VueRouter from 'vue-router'
import Select_page from "@/components/select_page";
import question_main_page from "@/components/question_main_page";

Vue.config.productionTip = false

Vue.use(ElementUI)
Vue.use(VueRouter)

const router = new VueRouter({
    routes: [
        {path: "/", component: Select_page},
        {path: "/question_main_page",name:"question_main_page", component: question_main_page}
    ]
})

new Vue({
    render: h => h(App),
    router: router,
}).$mount('#app')
