<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <el-button style="float: left;" type="primary" @click="previous_question" round>上一题
            </el-button>
            <span style="margin-top:3px;font-size: 25px;">试题</span>
            <el-button style="float: right;" type="primary" @click="next_question" round>下一题</el-button>
        </div>
        <div v-if="type_name==='single choice'">
            {{index+1}}.(单选)
            <div style="margin-top: 5px;" class="text" v-html="select_question[index].name"></div>
            <br/>
            <el-radio-group v-model="single_select_answer[index]" @change="set_select_answer($event,index)">
                <el-radio class="item" :label="1" border>{{select_question[index].A}}</el-radio>
                <br/><br/>
                <el-radio class="item" :label="2" border>{{select_question[index].B}}</el-radio>
                <br/><br/>
                <el-radio class="item" :label="3" border>{{select_question[index].C}}</el-radio>
                <br/><br/>
                <el-radio class="item" :label="4" border>{{select_question[index].D}}</el-radio>
                <br/>
            </el-radio-group>
        </div>
        <div v-if="type_name==='mulit choice'">
            {{index+1}}.(多选)
            <div style="margin-top: 5px;" class="text" v-html="select_question[index].name"></div>
            <br/>
            <el-checkbox-group v-model="mulit_select_answer[index]">
                <el-checkbox class="item" label="A" border>{{select_question[index].A}}
                </el-checkbox>
                <br/><br/>
                <el-checkbox class="item" label="B" border>{{select_question[index].B}}
                </el-checkbox>
                <br/><br/>
                <el-checkbox class="item" label="C" border>{{select_question[index].C}}
                </el-checkbox>
                <br/><br/>
                <el-checkbox class="item" label="D" border>{{select_question[index].D}}
                </el-checkbox>
                <br/>
                <div v-if="select_question[index].E!==''&&select_question[index].E!==null">
                    <el-checkbox class="item" label="E">{{select_question[index].E}}
                    </el-checkbox>
                    <br/>
                </div>
            </el-checkbox-group>
            <br/>
            <el-button type="primary" @click="check_mulit_select(index)" round>检查</el-button>
        </div>
    </el-card>
</template>

<script>
    import shitidata from "../assets/a.json"

    export default {
        name: 'question_main_page',
        components: {},
        mounted() {
            this.getShiTi();
            for (var i = 0; i < shitidata.data.length; i++) {
                this.mulit_select_answer[i] = [];
            }
        },
        create: {},
        methods: {
            getShiTi: function () {
                console.log(this.index);
                var shiti = {
                    type: shitidata.data[this.index].type,
                    id: shitidata.data[this.index].id,
                    name: shitidata.data[this.index].name,
                    A: shitidata.data[this.index].A,
                    B: shitidata.data[this.index].B,
                    C: shitidata.data[this.index].C,
                    D: shitidata.data[this.index].D,
                    E: shitidata.data[this.index].E,
                    answer: shitidata.data[this.index].answer
                };
                this.max_index = shitidata.data.length - 1;
                this.select_question.push(shiti);
                this.type_name = this.select_question[this.index].type;
            },
            shuzizhuanzimu: function (shuzi) {
                switch (shuzi) {
                    case 1:
                        return "A";
                    case 2:
                        return "B";
                    case 3:
                        return "C";
                    case 4:
                        return "D";
                    case 5:
                        return "E";
                    default :
                        break;
                }
            },
            check_mulit_select: function (id) {
                let that = this;
                console.log(this.mulit_select_answer[id]);
                if (this.mulit_select_answer[id].sort().toString() === shitidata.data[id].answer.sort().toString()) {
                    this.$alert('<h1>恭喜你，答对了！</h1>', '系统消息', {
                        confirmButtonText: '太棒了！下一题!',
                        dangerouslyUseHTMLString: true,
                        type: "success",
                        center: true,
                        callback: function (action) {
                            if (action === "confirm") {
                                if (that.index++ >= that.max_index) {
                                    that.index = that.max_index;
                                    that.$message({
                                        type: "error",
                                        showClose: true,
                                        duration: 2000,
                                        message: '没有下一题了哦!',
                                        center: true
                                    });
                                } else {
                                    that.getShiTi();
                                }
                            }
                        }
                    });
                } else {
                    this.$alert('<h1>答错了！</h1>', '系统消息', {
                        distinguishCancelAndClose: true,
                        showCancelButton: true,
                        confirmButtonText: '显示答案',
                        cancelButtonText: "继续思考",
                        dangerouslyUseHTMLString: true,
                        type: "error",
                        center: true,
                        callback: function (action) {
                            if (action === "confirm") {
                                that.$message({
                                    type: "success",
                                    showClose: true,
                                    duration: 2000,
                                    message: "正确答案是：" + shitidata.data[id].answer,
                                    center: true,
                                });
                            }
                        }
                    });
                }
            },
            set_select_answer: function (label, id) {
                let that = this;
                console.log(label + "||" + id);
                if (shitidata.data[id].answer === this.shuzizhuanzimu(label)) {
                    this.$alert('<h1>恭喜你，答对了！</h1>', '系统消息', {
                        confirmButtonText: '太棒了！下一题!',
                        dangerouslyUseHTMLString: true,
                        type: "success",
                        center: true,
                        callback: function (action) {
                            if (action === "confirm") {
                                if (that.index++ >= that.max_index) {
                                    that.index = that.max_index;
                                    that.$message({
                                        type: "error",
                                        showClose: true,
                                        duration: 2000,
                                        message: '没有下一题了哦!',
                                        center: true
                                    });
                                } else {
                                    that.getShiTi();
                                }
                            }
                        }
                    });
                } else {
                    this.$alert('<h1>答错了！</h1>', '系统消息', {
                        distinguishCancelAndClose: true,
                        showCancelButton: true,
                        confirmButtonText: '显示答案',
                        cancelButtonText: "继续思考",
                        dangerouslyUseHTMLString: true,
                        type: "error",
                        center: true,
                        callback: function (action) {
                            if (action === "confirm") {
                                that.$message({
                                    type: "success",
                                    showClose: true,
                                    duration: 2000,
                                    message: "正确答案是：" + shitidata.data[id].answer,
                                    center: true,
                                });
                            }
                        }
                    });
                }
            },
            previous_question: function () {
                if (this.index-- <= 0) {
                    this.index = 0;
                    this.$message({
                        type: "error",
                        showClose: true,
                        duration: 2000,
                        message: '没有上一题了哦!',
                        center: true
                    });
                }
                this.getShiTi();
            },
            next_question: function () {
                if (this.index++ >= this.max_index) {
                    this.index = this.max_index;
                    this.$message({
                        type: "error",
                        showClose: true,
                        duration: 2000,
                        message: '没有下一题了哦!',
                        center: true
                    });
                }
                this.getShiTi();
            }
        },
        data() {
            return {
                type_name: "",
                index: 0,
                max_index: 0,
                select_question: [],
                select_answer: [],
                single_select_answer: [],
                mulit_select_answer: [[], []],
            }
        }
    }
</script>

<style>
    .box-card {
        margin: 0 auto;
        width: 90%;
    }

    .clearfix {
        display: flex;
        justify-content: space-around;
    }

    .text {
        font-size: 20px;
    }

    .item {
        margin-top: 10px;
    }

</style>